* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
:root {
  --main-font: "Rubik", sans-serif;
  --color-menu: #4a4a4a;
  --color-title: #ffffff;
  --color-background: #3cdbc0;
  font-family: var(--main-font);
}

.header {
  background-color: var(--color-title);
  position: fixed;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.logo {
  width: 155px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.header-menu {
  display: flex;
  font-size: 16px;
  color: var(--color-menu);
  text-align: center;
  padding: 20px;
  list-style: none;
}
.menu-item {
  padding-top: 10px;
  padding-right: 10px;
  padding-bottom: 13px;
  padding-left: 13px;
}

.menu-link {
  text-decoration: none;
  color: var(--color-menu);
  padding: 10px;
  text-transform: uppercase;
}

.section {
  background-color: var(--color-background);
  display: flex;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.title {
  text-align: center;
  text-transform: capitalize;
  color: var(--color-title);
  font-size: 30px;
  word-spacing: 100vw;
}
@media all and (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    padding: 30px;
    font-size: 40px;
  }
  .title {
    font-size: 40px;
  }
}
